
<template>
	<div class="app-contain" :style='{"minHeight":"100vh","padding":"0","margin":"20px auto 60px","borderRadius":"0px","background":"#fff","width":"76%","position":"relative","height":"100%"}'>
		<div class="bread_view">
			<el-breadcrumb separator=">" class="breadcrumb">
				<el-breadcrumb-item class="first_breadcrumb" :to="{ path: '/' }"></el-breadcrumb-item>
				<el-breadcrumb-item class="second_breadcrumb" v-for="(item,index) in breadList" :key="index">{{item.name}}</el-breadcrumb-item>
			</el-breadcrumb>
		</div>
		<el-form ref="formRef" :model="form" class="add_form" label-width="120px" :rules="rules">
			<el-row>
				<el-col :span="8">
					<el-form-item label="" prop="caipinmingcheng">
						<el-input class="list_inp" v-model="form.caipinmingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.caipinmingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="12">
					<el-form-item label="" prop="caipintupian">
						<uploads
							:disabled="!isAdd||disabledForm.caipintupian?true:false"
							action="file/upload" 
							tip="" 
							:limit="3" 
							style="width: 100%;text-align: left;"
							:fileUrls="form.caipintupian?form.caipintupian:''" 
							@change="caipintupianUploadSuccess">
						</uploads>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="caipinfenlei">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.caipinfenlei?true:false"
							v-model="form.caipinfenlei" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in caipinfenleiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="(cal)" prop="kaluli">
						<el-input class="list_inp" v-model.number="form.kaluli" placeholder="(cal)"
							 type="number" 							:readonly="!isAdd||disabledForm.kaluli?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="(100g)" prop="tanshui">
						<el-input class="list_inp" v-model.number="form.tanshui" placeholder="(100g)"
							 type="number" 							:readonly="!isAdd||disabledForm.tanshui?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="(100g)" prop="danbaizhi">
						<el-input class="list_inp" v-model.number="form.danbaizhi" placeholder="(100g)"
							 type="number" 							:readonly="!isAdd||disabledForm.danbaizhi?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="(100g)" prop="zhifang">
						<el-input class="list_inp" v-model.number="form.zhifang" placeholder="(100g)"
							 type="number" 							:readonly="!isAdd||disabledForm.zhifang?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="(100g)" prop="yingyangsu">
						<el-input class="list_inp" v-model="yingyangsu" placeholder="(100g)" readonly></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="yingyangpingji">
						<el-select
							class="list_sel"
							:disabled="!isAdd||disabledForm.yingyangpingji?true:false"
							v-model="form.yingyangpingji" 
							placeholder=""
							style="width:100%;"
							>
							<el-option v-for="(item,index) in yingyangpingjiLists" :label="item"
								:value="item"
								>
							</el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="8">
					<el-form-item label="" prop="caipinjianjie">
						<el-input class="list_inp" v-model="form.caipinjianjie" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.caipinjianjie?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="shangjiazhanghao">
						<el-input class="list_inp" v-model="form.shangjiazhanghao" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiazhanghao?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="shangjiamingcheng">
						<el-input class="list_inp" v-model="form.shangjiamingcheng" placeholder=""
							 type="text" 							:readonly="!isAdd||disabledForm.shangjiamingcheng?true:false" />
					</el-form-item>
				</el-col>

				<el-col :span="8">
					<el-form-item label="" prop="price">
						<el-input class="list_inp" v-model.number="form.price" placeholder=""
							 type="number" 							:readonly="!isAdd||disabledForm.price?true:false" />
					</el-form-item>
				</el-col>

			</el-row>
			<div class="formModel_btn_box">
				<el-button class="formModel_cancel" @click="backClick"></el-button>
				<el-button class="formModel_confirm" @click="save"
					type="success"
					>
					
				</el-button>
			</div>
		</el-form>
	</div>
</template>
<script setup>
	import {
		ref,
		getCurrentInstance,
		watch,
		onUnmounted,
		onMounted,
		nextTick,
		computed
	} from 'vue';
	import {
		useRoute,
		useRouter
	} from 'vue-router';
	const context = getCurrentInstance()?.appContext.config.globalProperties;
	const route = useRoute()
	const router = useRouter()
	//
	const tableName = 'caipinxinxi'
	const formName = ''
	//
	const breadList = ref([{
		name: formName
	}])
	//
	const getUUID =()=> {
      return new Date().getTime();
    }
	//form
	const form = ref({
		caipinmingcheng: '',
		caipintupian: '',
		caipinfenlei: '',
		kaluli: '',
		tanshui: '',
		danbaizhi: '',
		zhifang: '',
		yingyangsu: '',
		yingyangpingji: '',
		caipinjianjie: '',
		shangjiazhanghao: '',
		shangjiamingcheng: '',
		storeupnum: '0',
		clicktime: '',
		price: '0',
		clicknum: '0',
		thumbsupnum: '0',
		crazilynum: '0',
	})
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	const disabledForm = ref({
		caipinmingcheng : false,
		caipintupian : false,
		caipinfenlei : false,
		kaluli : false,
		tanshui : false,
		danbaizhi : false,
		zhifang : false,
		yingyangsu : false,
		yingyangpingji : false,
		caipinjianjie : false,
		shangjiazhanghao : false,
		shangjiamingcheng : false,
		storeupnum : false,
		clicktime : false,
		price : false,
		clicknum : false,
		thumbsupnum : false,
		crazilynum : false,
	})
	const isAdd = ref(false)
	//
	//
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error(""));
		} else {
			callback();
		}
	}
	//
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("URL"));
		} else {
			callback();
		}
	}
	const rules = ref({
		caipinmingcheng: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		caipintupian: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		caipinfenlei: [
			{required: true,message: '',trigger: 'blur'}, 
		],
		kaluli: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		tanshui: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		danbaizhi: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		zhifang: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		yingyangsu: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		yingyangpingji: [
		],
		caipinjianjie: [
		],
		shangjiazhanghao: [
		],
		shangjiamingcheng: [
		],
		storeupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		clicktime: [
		],
		price: [
			{ validator: validateNumber, trigger: 'blur' },
		],
		clicknum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		thumbsupnum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
		crazilynum: [
			{ validator: validateIntNumber, trigger: 'blur' },
		],
	})
	//
	const caipintupianUploadSuccess=(e)=>{
		form.value.caipintupian = e
	}
	//
	const caipinfenleiLists = ref([])
	//
	const yingyangpingjiLists = ref([])
	const yingyangsu =computed(()=>{
		let c = form.value
		let a = c.tanshui+c.danbaizhi+c.zhifang
		form.value.yingyangsu = a?a.toFixed(2):0
		return a?a.toFixed(2):0
	})
	//methods

	//methods
	//info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			form.value = res.data.data
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//
	const init = (formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null) => {
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			// getInfo()
			for(let x in row){
				if(x=='caipinmingcheng'){
					form.value.caipinmingcheng = row[x];
					disabledForm.value.caipinmingcheng = true;
					continue;
				}
				if(x=='caipintupian'){
					form.value.caipintupian = row[x];
					disabledForm.value.caipintupian = true;
					continue;
				}
				if(x=='caipinfenlei'){
					form.value.caipinfenlei = row[x];
					disabledForm.value.caipinfenlei = true;
					continue;
				}
				if(x=='kaluli'){
					form.value.kaluli = row[x];
					disabledForm.value.kaluli = true;
					continue;
				}
				if(x=='tanshui'){
					form.value.tanshui = row[x];
					disabledForm.value.tanshui = true;
					continue;
				}
				if(x=='danbaizhi'){
					form.value.danbaizhi = row[x];
					disabledForm.value.danbaizhi = true;
					continue;
				}
				if(x=='zhifang'){
					form.value.zhifang = row[x];
					disabledForm.value.zhifang = true;
					continue;
				}
				if(x=='yingyangsu'){
					form.value.yingyangsu = row[x];
					disabledForm.value.yingyangsu = true;
					continue;
				}
				if(x=='yingyangpingji'){
					form.value.yingyangpingji = row[x];
					disabledForm.value.yingyangpingji = true;
					continue;
				}
				if(x=='caipinjianjie'){
					form.value.caipinjianjie = row[x];
					disabledForm.value.caipinjianjie = true;
					continue;
				}
				if(x=='shangjiazhanghao'){
					form.value.shangjiazhanghao = row[x];
					disabledForm.value.shangjiazhanghao = true;
					continue;
				}
				if(x=='shangjiamingcheng'){
					form.value.shangjiamingcheng = row[x];
					disabledForm.value.shangjiamingcheng = true;
					continue;
				}
				if(x=='storeupnum'){
					form.value.storeupnum = row[x];
					disabledForm.value.storeupnum = true;
					continue;
				}
				if(x=='clicktime'){
					form.value.clicktime = row[x];
					disabledForm.value.clicktime = true;
					continue;
				}
				if(x=='price'){
					form.value.price = row[x];
					disabledForm.value.price = true;
					continue;
				}
				if(x=='clicknum'){
					form.value.clicknum = row[x];
					disabledForm.value.clicknum = true;
					continue;
				}
				if(x=='thumbsupnum'){
					form.value.thumbsupnum = row[x];
					disabledForm.value.thumbsupnum = true;
					continue;
				}
				if(x=='crazilynum'){
					form.value.crazilynum = row[x];
					disabledForm.value.crazilynum = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			form.value.storeupnum='0'
			form.value.price='0'
			form.value.clicknum='0'
			form.value.thumbsupnum='0'
			form.value.crazilynum='0'
		}
		context?.$http({
			url: `${context?.$toolUtil.storageGet('frontSessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('shangjiazhanghao') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.shangjiazhanghao = json.shangjiazhanghao
				disabledForm.value.shangjiazhanghao = true;
			}
			if(json.hasOwnProperty('shangjiamingcheng') && context?.$toolUtil.storageGet("frontRole")!=""){
				form.value.shangjiamingcheng = json.shangjiamingcheng
				disabledForm.value.shangjiamingcheng = true;
			}
		})
		context?.$http({
			url: `option/caipinfenlei/caipinfenlei`,
			method: 'get'
		}).then(res=>{
			caipinfenleiLists.value = res.data.data
		})
		yingyangpingjiLists.value = ",,".split(',')
	}
	//
	//
	const backClick = () => {
		history.back()
	}
	//
	const save=()=>{
		if(form.value.caipintupian!=null) {
			form.value.caipintupian = form.value.caipintupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(``,'success',()=>{
									history.back()
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(``,'success',()=>{
							history.back()
						})
					})
				}
			}
		})
	}
	//
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
	onMounted(()=>{
		type.value = route.query.type?route.query.type:'add'
		let row = null
		let table = null
		let statusColumnName = null
		let tips = null
		let statusColumnValue = null
		if(type.value == 'cross'){
			row = context?.$toolUtil.storageGet('crossObj')?JSON.parse(context?.$toolUtil.storageGet('crossObj')):{}
			table = context?.$toolUtil.storageGet('crossTable')
			statusColumnName = context?.$toolUtil.storageGet('crossStatusColumnName')
			tips = context?.$toolUtil.storageGet('crossTips')
			statusColumnValue = context?.$toolUtil.storageGet('crossStatusColumnValue')
		}
		init(route.query.id?route.query.id:null, type.value,'', row, table, statusColumnName, tips, statusColumnValue)
	})
	
</script>
<style lang="scss" scoped>
	// 
	.bread_view {
		border-radius: 0px;
		padding: 12px 0px;
		margin: 0px auto;
		background: none;
		width: 100%;
		border-color: #eee;
		border-width: 0 0 0px;
		position: relative;
		border-style: solid;
		:deep(.breadcrumb) {
			font-size: 14px;
			line-height: 1;
			.el-breadcrumb__separator {
				margin: 0 9px;
				color: #999;
				font-weight: 500;
			}
			.first_breadcrumb {
				.el-breadcrumb__inner {
					color: #333;
					display: inline-block;
				}
			}
			.second_breadcrumb {
				.el-breadcrumb__inner {
					color: #999;
					display: inline-block;
				}
			}
		}
	}
	// 
	.add_form{
		border: 1px solid #ddd;
		border-radius: 0px;
		padding: 40px 7% 50px;
		background: #fff;
		// form item
		:deep(.el-form-item) {
			border: 0px solid #eee;
			padding: 6px 0;
			margin: 0 0 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 display: block;
			 width: auto;
			 min-width: 150px;
			 text-align: right;
			}
			// 
			.el-form-item__content {
				display: flex;
				width: calc(100% - 150px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 
				.list_inp {
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					height: 36px;
					//
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 
				.list_sel {
					border-radius: 0px;
					padding: 0 10px;
					background: #fff;
					width: auto;
					border-color: #ccc;
					border-width: 1px;
					line-height: 36px;
					box-sizing: border-box;
					border-style: solid;
					min-width: 100%;
					//
					.select-trigger{
						height: 100%;
						.el-input{
							height: 100%;
							.el-input__wrapper{
								border: none;
								box-shadow: none;
								background: none;
								border-radius: 0;
								height: 100%;
